*{
    font-family: 'Courier New', Courier, monospace;
}

body{
    padding: 60px;
    color: white;
    background-color: black;
}

input{
    width: 100%;
    font-size: 1.2rem;
    color: grey;
}

.card{
    align-content: center;
    border: 1px solid grey;
    border-radius: 6px;
    margin-bottom: 10px;
    margin-right: 10px;
    overflow: hidden;
}

.card p{
    max-height: 170px;
}

.card-grid{
    display:flex;
    flex-direction: row;
    flex-wrap: wrap;

}